import { connect } from 'react-redux';
import Comment from './Comment';
import {thumbUpComment, thumbDownComment, removeComment, editComment} from './actions';

const mapDispatchToProps = dispatch => ({
  thumbUpComment: (id) => dispatch(thumbUpComment(id)),
  thumbDownComment: (id) => dispatch(thumbDownComment(id)),
  removeComment: (id) => dispatch(removeComment(id)),
  editComment: (id, text) => dispatch(editComment(id, text))
});

export default connect(null, mapDispatchToProps)(Comment);

// Czyli, e jak z komponentu Comment wywoam np. editComment to tak naprawd wywoam dispatch(editComment)
// czy to jest co w stylu przekazania propsa editComment={dispatch(editComment(id, text))}

// Ale w zwizku z tym, e stan aplikacji trzymam w store reduxowym to uywam sobie connecta by wstrzykiwa te propsy i dlatego mog uywa bezstanowych komponentw, bo nie potrzebuj uywa this.state reactowego.



